<template>
 <div class="center">
    <div class="center_breadcrumb">
      <crumb style="padding: 0 5px;">
        <!-- <div class="new--btn" slot="right" @click="openAdd">+ 新建</div> -->
      </crumb>
    </div>
    <div class="center_content">
        <!-- <div class="center_content_title">
            <SvgIcon icon-class="icondatixitongguanli" size="20px"></SvgIcon>
            <span>{{pageTitle}}</span>
        </div> -->
        <div class="center_content_search">
            <div class="center_content_search_left">
                <div class="search-input-suffix gap">
                    <label>操作模块：</label>
                    <el-select v-model="page.model" placeholder="请选择">
                        <el-option
                        v-for="item in directoryProgramOptions"
                        :key="item.code"
                        :label="item.value"
                        :value="item.code">
                        </el-option>
                    </el-select>
                </div>
                <div class="search-input-suffix">
                    <label>日志搜索：</label>
                    <el-input class="search"
                            @keyup.enter.native="search"
                            v-model="page.operation"
                            placeholder="请输入搜索关键词"
                            clearable
                            >
                    </el-input>
                    <!-- <el-button @click="search" type="primary">搜索</el-button> -->
                </div>
                <div class="search-input-suffix" style="margin: 0 0 0 20px">
                  <div class="search--btn"  @click="search">搜索</div>
                </div>
            </div>
        </div>
         <div class="table-container">
             <el-table
                :data="tableData"
                stripe
                style="width: 100%">
                <el-table-column
                prop="creatorVal"
                label="用户名">
                </el-table-column>
                <el-table-column
                prop="ip"
                label="ip">
                </el-table-column>
                <el-table-column
                prop="operation"
                label="描述">
                </el-table-column>
                <el-table-column
                prop="createDate"
                label="创建时间">
                </el-table-column>
            </el-table>
            <div class="center_pagination">
                <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    background
                    :current-page="page.page"
                    :page-sizes="[15,30,40,50]"
                    :page-size="page.rows"
                    layout="prev, pager, next, jumper, sizes, total"
                    :total="data.total">
                </el-pagination>
            </div>
        </div>
    </div>
 </div>
</template>

<script>
import systemApi from '@/api/systemApi'
export default {
    name: 'LogManagement',
    data() {
        return {
            pageTitle:'日志管理',
            data:{},
            tableData: [],
            directoryProgramOptions: [],
            page:{
                model:null,
                operation:'',
                page:1,
                rows:15
            }
            
        }
    },
    created(){},
    mounted(){
        this.initData()
        this.sysMenuAll()
    },
    methods:{
        initData(){
            const url =systemApi.sysLogList;
            this.$post('/system/sysLog/list',this.page).then(res =>{
                if(res.status === 1){
                    this.data=res;
                    this.tableData=res.data;                 
                }else{
                this.$notify.error({
                    title: 'error',
                    duration:1500,
                    message: res.message
                    })
                }
            }).catch(err =>{
            console.log(err)
            })
        },
        sysMenuAll(){
            const url =systemApi.sysMenuAll;
            this.$get('/system/dict/field/Modular').then(res =>{
                if(res.status === 1){
                    const directoryObj={
                        code: null,
                        value:'全部'
                    }
                    res.data.unshift(directoryObj);
                    this.directoryProgramOptions=res.data;
                    }else{
                    this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: res.message
                    })
                }
            }).catch(err =>{
                console.log(err)
            })
        },
        headInfo(item){
            this.pageTitle=item[1]
        },
        search(){
            this.resetTemp();
            this.initData() ;
        },
        handleSizeChange(val){
            this.page.rows = val;
            this.initData()
        },
        handleCurrentChange(obj){
            this.page.page = obj;
            this.initData()
        },
        resetTemp(){
            this.page.page=1;
            this.page.rows=15;
        }

    }
}
</script>

<style lang='scss'>
  @import '../../styles/center.scss'
</style>



